<script>
import axios from "axios";

import Layout from "@/layouts/auth";
import { authMethods } from "@/state/helpers";
import appConfig from "@/app.config";

import { required, email } from "vuelidate/lib/validators";

/**
 * Forgot Password component
 */
export default {
  page: {
    title: "Forgot Password",
    meta: [
      {
        name: "description",
        content: appConfig.description,
      },
    ],
  },
  components: {
    Layout,
  },
  data() {
    return {
      email: "",
      submitted: false,
      error: null,
      tryingToReset: false,
      isResetError: false,
    };
  },
  validations: {
    email: {
      required,
      email,
    },
  },
  methods: {
    ...authMethods,
    // Try to register the user in with the email, fullname
    // and password they provided.
    tryToReset() {
      this.submitted = true;
      // stop here if form is invalid
      this.$v.$touch();

      if (this.$v.$invalid) {
        return;
      } else {
        if (process.env.VUE_APP_DEFAULT_AUTH === "firebase") {
          this.tryingToReset = true;
          // Reset the authError if it existed.
          this.error = null;
          return (
            this.resetPassword({
              email: this.email,
            })
              // eslint-disable-next-line no-unused-vars
              .then((token) => {
                this.tryingToReset = false;
                this.isResetError = false;
              })
              .catch((error) => {
                this.tryingToReset = false;
                this.error = error ? error : "";
                this.isResetError = true;
              })
          );
        } else if (process.env.VUE_APP_DEFAULT_AUTH === "authapi") {
          axios
            .post("http://127.0.0.1:8000/api/password/create", {
              email: this.email,
            })
            .then((res) => {
              this.isResetError = true;
              this.error = res.data;
              return res;
            });
        }
      }
    },
  },
};
</script>

<template>
  <Layout>
    <div class="row justify-content-center">
      <div class="col-md-8 col-lg-6 col-xl-5">
        <div class="card overflow-hidden">
          <div class="bg-soft-primary">
            <div class="row">
              <div class="col-7">
                <div class="text-primary p-4">
                  <h5 class="text-primary">비밀 번호 초기화</h5>
                  <p>비밀번호를 재설정하세요</p>
                </div>
              </div>
              <div class="col-5 align-self-end">
                <img
                  src="@/assets/images/powerplant-4.png"
                  alt
                  class="img-fluid login-img-gradient"
                />
              </div>
            </div>
          </div>
          <div class="card-body pt-0">
            <div class="p-2">
              <b-alert
                v-model="isResetError"
                class="mb-4"
                variant="danger"
                dismissible
                >{{ error }}</b-alert
              >
              <form @submit.prevent="tryToReset">
                <div class="form-group">
                  <label for="useremail">이전비밀번호</label>
                  <input
                    type="password"
                    v-model="email"
                    class="form-control"
                    id="useremail"
                    placeholder="이전비밀번호"
                    :class="{ 'is-invalid': submitted && $v.email.$error }"
                  />
                  <div
                    v-if="submitted && $v.email.$error"
                    class="invalid-feedback"
                  >
                    <span v-if="!$v.email.required">이전 비밀번호를 입력하세요.</span>
                    <span v-if="!$v.email.email"
                      >이전 비밀번호를 입력하세요.</span
                    >
                  </div>
                </div>
                <div class="form-group">
                  <label for="useremail">새 비밀번호</label>
                  <input
                    type="password"
                    v-model="email"
                    class="form-control"
                    id="useremail"
                    placeholder="새 비밀번호"
                    :class="{ 'is-invalid': submitted && $v.email.$error }"
                  />
                  <div
                    v-if="submitted && $v.email.$error"
                    class="invalid-feedback"
                  >
                    <span v-if="!$v.email.required">새 비밀번호를 입력하세요.</span>
                    <span v-if="!$v.email.email"
                      >새 비밀번호를 입력하세요.</span
                    >
                  </div>
                </div>
                <div class="form-group">
                  <label for="useremail">새 비밀번호 반복</label>
                  <input
                    type="password"
                    v-model="email"
                    class="form-control"
                    id="useremail"
                    placeholder="새 비밀번호 반복"
                    :class="{ 'is-invalid': submitted && $v.email.$error }"
                  />
                  <div
                    v-if="submitted && $v.email.$error"
                    class="invalid-feedback"
                  >
                    <span v-if="!$v.email.required">새 비밀번호를 다시 입력하세요.</span>
                    <span v-if="!$v.email.email"
                      >새 비밀번호를 다시 입력하세요.</span
                    >
                  </div>
                </div>
                <div class="form-group row mb-0">
                  <div class="col-12 text-right">
                    <button class="btn btn-primary w-md" type="submit">
                      재설정
                    </button>
                  </div>
                </div>
              </form>
            </div>
          </div>
          <!-- end card-body -->
        </div>
        <!-- end card -->


        <div class="mt-5 text-center">
          <p>
            정상로그인이 안되는경우 확인하세요
            <router-link
              tag="a"
              to="#"
              class="font-weight-medium text-primary"
              >계정발급절차</router-link
            >
          </p>
          <p class="text-right">
             ©2021 로제타텍            
          </p>
        </div>

      </div>
      <!-- end col -->
    </div>
    <!-- end row -->
  </Layout>
</template>

<style lang="scss" module></style>
